import styled from 'styled-components';

type TSectionProps = {
  flex?: boolean;
  textAlign?: 'center' | 'left' | 'right';
  alignItems?: 'center' | 'flex-start' | 'flex-end';
  fullscreen?: boolean;
  first?: boolean;
  mode?: 'dark' | 'light';
};

export default styled.section<TSectionProps>`
  width: 100%;
  padding: ${props => (props.first ? props.theme.headerHeight : 0)} ${props => props.theme.headerHorizontalPadding} 8vh;
  padding-bottom: 8vh;
  display: ${p => p.flex ? 'flex' : 'block'};
  justify-content: center;
  align-items: ${props => props.alignItems};
  text-align: ${props => props.textAlign};
  height: ${props => (props.fullscreen ? '100vh' : 'initial')};
  box-sizing: ${props => (props.fullscreen ? ' border-box' : 'initial')};
  background-color: ${p =>
    p.mode === 'light' ? '#fff' : p.theme.colors.black};
`;